@media only screen and (max-width: 767px) { .video { padding-top: 25px; } }

.video__title { margin-bottom: 63px; }

@media only screen and (max-width: 767px) { .video__title { margin-bottom: 25px; } }

.about { padding-top: 70px; }

@media only screen and (max-width: 767px) { .about { padding-top: 25px; } }

.about__title { margin-bottom: 47px; }

@media only screen and (max-width: 767px) { .about__title { margin-bottom: 25px; } }

.about__content { position: relative; padding: 38px 0 46px; background-size: cover; background-repeat: no-repeat; background-position: center; background-attachment: fixed; transform: translate3d(0, 0, 0); }

.about__text { padding: 50px 35px 30px; max-width: 680px; background-color: #fff; display: flex; flex-direction: column; align-items: flex-end; }

@media only screen and (max-width: 767px) { .about__text { padding: 20px; } }

.about__item:not(:last-child) { margin-bottom: 22px; }

.about__topic { margin-bottom: 14px; }

.about__list-item { position: relative; padding-left: 10px; }

.about__list-item:before { content: ""; position: absolute; width: 5px; height: 5px; background-color: #363636; left: 0; top: 8px; border-radius: 50%; }

.price { padding: 50px 0 40px; background-color: #f6f7fd; }

@media only screen and (max-width: 767px) { .price { padding: 25px 0; } }

.price__title { margin-bottom: 50px; }

@media only screen and (max-width: 767px) { .price__title { margin-bottom: 25px; } }

@media only screen and (max-width: 1023px) { .price__list { justify-content: center; } }

.price__note { color: #666696; font-size: 16px; margin-top: 36px; }

@media only screen and (max-width: 767px) { .price__note { font-size: 14px; margin-top: 15px; } }

.price__note span { color: #db019d; }

@media only screen and (max-width: 1023px) { .price .col:not(:last-child) { margin-bottom: 20px; } }

@media only screen and (max-width: 767px) { .price .col:not(:last-child) { margin-bottom: 15px; } }

.price-item { position: relative; z-index: 1; padding: 35px 43px 42px; text-align: center; }

.price-item:hover:before { transform: scale(1.03); }

@media only screen and (max-width: 599px) { .price-item { padding: 15px; } }

.price-item:before { position: absolute; content: ""; top: 0; left: 0; z-index: -1; width: 100%; height: 100%; transition: transform .2s ease-in-out; box-shadow: 0 0 15px rgba(184, 184, 184, 0.48); background-color: #fff; }

.price-item:last-child { margin-bottom: 0; }

.price-item__subtitle { position: relative; margin: 5px 0 25px; font-size: 14px; line-height: 1.57; min-height: 65px; }

@media only screen and (max-width: 767px) { .price-item__subtitle { margin-bottom: 15px; min-height: auto; padding-bottom: 8px; } }

.price-item__subtitle:after { content: ""; position: absolute; background-color: #db019d; box-shadow: 0px 0px 22px 0px rgba(184, 184, 184, 0.48); position: absolute; width: 234px; height: 3px; bottom: 0; left: 50%; transform: translateX(-50%); }

.price-item__cost { position: relative; margin-bottom: 32px; }

@media only screen and (max-width: 767px) { .price-item__cost { margin-bottom: 20px; } }

.price-item__value { display: block; margin-top: 5px; font-size: 50px; font-weight: 500; line-height: 1.12; }

@media only screen and (max-width: 767px) { .price-item__value { font-size: 32px; } }

.price-item__hint { position: absolute; left: 0; bottom: -24; width: 100%; text-align: center; font-size: 18px; font-weight: 500; }

@media only screen and (max-width: 767px) { .price-item__hint { font-size: 14px; } }

.price-item__hint span { color: #db019d; }

.price-item__time { font-size: 25px; margin-bottom: 25px; }

@media only screen and (max-width: 767px) { .price-item__time { font-size: 18px; margin-bottom: 15px; } }

.students { padding: 60px 0 70px; }

@media only screen and (max-width: 767px) { .students { padding: 25px 0; } }

.students__title { margin-bottom: 50px; }

@media only screen and (max-width: 767px) { .students__title { margin-bottom: 25px; } }

.students__list { box-shadow: 0 0 15px rgba(184, 184, 184, 0.48); }

@media only screen and (max-width: 1023px) { .students__list { padding-bottom: 35px; } }

.students-item { padding: 35px 60px 40px; text-align: center; font-size: 14px; line-height: 2.357; }

@media only screen and (max-width: 599px) { .students-item { padding: 20px 15px; } }

.students-item__title { margin-bottom: 10px; }

.students-item__subtitle { margin-bottom: 15px; }

.students-item__img { width: 166px; height: 166px; border-radius: 50%; background-position: center; background-repeat: no-repeat; background-size: cover; display: inline-block; vertical-align: top; border: 1px solid #fff; box-shadow: 0 0 22px rgba(184, 184, 184, 0.48); margin-bottom: 35px; }

@media only screen and (max-width: 767px) { .students-item__img { margin-bottom: 15px; } }

.contacts__top { padding: 53px 0 13px; }

@media only screen and (max-width: 767px) { .contacts__top { padding: 20px 0 13px; } }

.contacts__tel { display: block; font-size: 30px; font-weight: 900; color: #363636; text-decoration: none; }

@media only screen and (max-width: 767px) { .contacts__tel { font-size: 18px; } }

.contacts__email { display: block; font-size: 25px; color: #db019d; font-weight: 700; margin-top: 28px; }

@media only screen and (max-width: 767px) { .contacts__email { font-size: 18px; margin-top: 15px; } }

.contacts__center { padding: 50px 0 65px; background-color: #f6f7fd; }

@media only screen and (max-width: 767px) { .contacts__center { padding: 25px 0; } }

.contacts__list { margin-top: 30px; }

@media only screen and (max-width: 767px) { .contacts__list { margin-top: 20px; } }

.contacts__item { position: relative; font-size: 17px; color: #1616a6; font-weight: 500; padding-left: 15px; }

@media only screen and (max-width: 767px) { .contacts__item { font-size: 15px; } }

.contacts__item:before { content: ""; position: absolute; width: 6px; height: 6px; background-color: #1616a6; left: 0; top: 9px; border-radius: 50%; }

.contacts__item_bold { font-weight: 700; font-size: 19px; }

@media only screen and (max-width: 767px) { .contacts__item_bold { font-size: 15px; } }

.footer { padding: 40px 0 25px; overflow: hidden; }

@media only screen and (max-width: 767px) { .footer { padding: 20px; } }

.footer__ad { font-size: 16px; font-weight: 700; text-align: right; line-height: 1; color: #a8a8a8; }

@media only screen and (max-width: 767px) { .footer__ad { font-size: 12px; } }

.footer__ad a { color: inherit; text-decoration: none; transition: color .3s; }

.footer__ad a:hover { color: #424242; }

.footer__up { position: fixed; left: 0; z-index: 100; text-align: right; bottom: 115px; width: 100%; pointer-events: none; }

@media only screen and (max-width: 1023px) { .footer__up { display: none; } }

.footer__container { position: relative; }

@media (min-width: 768px) { .footer__container { width: 100%; } }

.btn-up { margin-right: 30px; font-size: 0; pointer-events: initial; opacity: 0; visibility: hidden; transition: opacity .4s, visibility .4s; background-color: transparent; }

.btn-up.is-show { visibility: visible; opacity: 1; }

.slider { margin: 0 -15px; }

@media only screen and (max-width: 1023px) { .slider { margin: 0 -7px; } }

.slider__slide { padding: 0 15px; }

@media only screen and (max-width: 1023px) { .slider__slide { padding: 0 7px; } }

.slider .slick-dots { text-align: center; }

.slider .slick-dots li { vertical-align: top; display: inline-block; }

.slider .slick-dots li:not(:first-child) { margin-left: 15px; }

.slider .slick-dots li.slick-active button { background-color: #db019d; }

.slider .slick-dots button { position: relative; font-size: 0; width: 10px; height: 10px; background-color: #fe77d8; border-radius: 50%; transition: background-color .3s; }

.slider .slick-dots button:before { position: absolute; content: ""; top: -10px; right: -10px; bottom: -10px; left: -10px; }
