.price {
	padding: 50px 0 40px;
	background-color: #f6f7fd;
	@include sm {
		padding: 25px 0; }
	&__title {
		margin-bottom: 50px;
		@include sm {
			margin-bottom: 25px; } }
	&__list {
		@include md {
			justify-content: center; } }
	&__note {
		color: #666696;
		font-size: 16px;
		margin-top: 36px;
		@include sm {
			font-size: 14px;
			margin-top: 15px; }
		span {
			color: $pink; } }
	.col {
		&:not(:last-child) {
			@include md {
				margin-bottom: 20px; }
			@include sm {
				margin-bottom: 15px; } } } }

.price-item {
	position: relative;
	z-index: 1;
	padding: 35px 43px 42px;
	text-align: center;
	&:hover {
		&:before {
			@include scale(1.03); } }
	@include xs {
		padding: 15px; }
	&:before {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		transition: transform .2s ease-in-out;
		box-shadow: 0 0 15px rgba(#b8b8b8,.48);
		background-color: $white; }
	&:last-child {
		margin-bottom: 0; }
	&__subtitle {
		position: relative;
		margin: 5px 0 25px;
		font-size: 14px;
		line-height: 1.57;
		min-height: 65px;
		@include sm {
			margin-bottom: 15px;
			min-height: auto;
			padding-bottom: 8px; }
		&:after {
			content: "";
			position: absolute;
			background-color: $pink;
			box-shadow: 0px 0px 22px 0px rgba(184, 184, 184, 0.48);
			position: absolute;
			width: 234px;
			height: 3px;
			bottom: 0;
			left: 50%;
			transform: translateX(-50%); } }
	&__cost {
		position: relative;
		margin-bottom: 32px;
		@include sm {
			margin-bottom: 20px; } }
	&__value {
		display: block;
		margin-top: 5px;
		font-size: 50px;
		font-weight: 500;
		line-height: 1.12;
		@include sm {
			font-size: 32px; } }
	&__hint {
		position: absolute;
		left: 0;
		bottom: -24;
		width: 100%;
		text-align: center;
		font-size: 18px;
		font-weight: 500;
		@include sm {
			font-size: 14px; }
		span {
			color: $pink; } }
	&__time {
		font-size: 25px;
		margin-bottom: 25px;
		@include sm {
			font-size: 18px;
			margin-bottom: 15px; } } }
